import React from "react"
import VisibilitySensor from 'react-visibility-sensor';

const ModuleViewed = ({children}) => {
    return (
        <VisibilitySensor>
            {({isVisible}) => {
                <div>I am {isVisible ? 'visible' : 'invisible'}</div>
                {children}
            }}
          
        </VisibilitySensor>
    )
}

export default ModuleViewed

